export default function Projects({ Desktop, Tablet, Mobile }) {
  return (
    <div id="Projects">
      {
        <Desktop>
          <div className="in-Projects">
            <h2 className="sec-head">Projects</h2>
            {data.map((i) => (
              <div key={i.name} className="project-card">
                <div className="project-card-img">
                  <img src={i.img} alt={i.name} />
                </div>
                <div className="project-card-desc-div">
                  <h1 className="P-name">{i.name}</h1>
                  <h3 className="P-desc">{i.desc}</h3>
                  <h2 className="tech-head">Tech Stack :</h2>
                  <div>
                    {i.tech.map((t) => (
                      <Badge variant="subtle" colorScheme="purple" mr=".6rem">
                        {t}
                      </Badge>
                    ))}
                  </div>
                  {/* <h3 className="techs">{i.tech.join(" | ")}</h3> */}
                  <div className="P-btn-div">
                    <a href={i.repo}>
                      <Button
                        leftIcon={github()}
                        color="white"
                        bgColor="black"
                        _hover={{backgroundColor:"none"}}
                        variant="solid"
                      >
                        Github
                      </Button>
                      {/* <button className="P-btn">repo</button> */}
                    </a>
                    <a href={i.live}>
                    <Button
                        // leftIcon={github()}
                        color="white"
                        w="7.6rem"
                        bgColor="black"
                        variant="solid"
                        _hover={{backgroundColor:"none"}}
                        ml="1rem"
                      >
                        Live
                      </Button>
                      {/* <button class="P-btn">live</button> */}
                    </a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </Desktop>
      }
      {
        <Tablet>
          <div className="in-Projects">
            <h2 className="T-sec-head">Projects</h2>
            {data.map((i) => (
              <div key={i.name} className="T-project-card">
                <div >
                  <img src={i.img} alt={i.name} />
                </div>
                <div >
                  <h1 className="P-name">{i.name}</h1>
                  <h3 className="P-desc">{i.desc}</h3>
                  <h2 className="tech-head">Tech Stack :</h2>
                  <div>
                    {i.tech.map((t) => (
                      <Badge variant="subtle" colorScheme="purple" mr=".6rem">
                        {t}
                      </Badge>
                    ))}
                  </div>
                  {/* <h3 className="techs">{i.tech.join(" | ")}</h3> */}
                  <div className="T-P-btn-div">
                    <a href={i.repo}>
                      <Button
                        leftIcon={github()}
                        color="white"
                        bgColor="black"
                        _hover={{backgroundColor:"none"}}
                        variant="solid"
                      >
                        Github
                      </Button>
                      {/* <button className="P-btn">repo</button> */}
                    </a>
                    <a href={i.live}>
                    <Button
                        // leftIcon={github()}
                        color="white"
                        w="7.6rem"
                        bgColor="black"
                        variant="solid"
                        _hover={{backgroundColor:"none"}}
                        ml="1rem"
                      >
                        Live
                      </Button>
                      {/* <button class="P-btn">live</button> */}
                    </a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </Tablet>
      }
      {
        <Mobile>
          <div className="M-in-Projects">
            <h2 className="M-sec-head">Projects</h2>
            {data.map((i) => (
              <div key={i.name} className="T-project-card">
                <div >
                  <img src={i.img} alt={i.name} />
                </div>
                <div >
                  <h1 className="P-name">{i.name}</h1>
                  <h3 className="P-desc">{i.desc}</h3>
                  <h2 className="tech-head">Tech Stack :</h2>
                  <div>
                    {i.tech.map((t) => (
                      <Badge variant="subtle" colorScheme="purple" mr=".6rem">
                        {t}
                      </Badge>
                    ))}
                  </div>
                  {/* <h3 className="techs">{i.tech.join(" | ")}</h3> */}
                  <div className="P-btn-div">
                    <a href={i.repo}>
                      <Button
                        leftIcon={github()}
                        color="white"
                        bgColor="black"
                        _hover={{backgroundColor:"none"}}
                        variant="solid"
                      >
                        Github
                      </Button>
                      {/* <button className="P-btn">repo</button> */}
                    </a>
                    {/* <br/> */}
                    <a href={i.live}>
                    <Button
                        // leftIcon={github()}
                        color="white"
                        w="7rem"
                        bgColor="black"
                        variant="solid"
                        // mt=".5rem"
                        _hover={{backgroundColor:"none"}}
                      >
                        Live
                      </Button>
                      {/* <button class="P-btn">live</button> */}
                    </a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </Mobile>
      }
    </div>
  );
}
